const WxAuth = require('@/config/WxAuth');
export default {
	data() {
		return {
			mobile: {
				phone: '',
				code: ''
			},
			// 验证码
			safety: {
				time: 60,
				state: false,
				interval: ''
			},
			loginMobile: false,
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
		}
	},
	watch: {
		/**
		 * 监听手机登录数值
		 */
		mobile: {
			handler(newValue) {
				if (this.phoneReg.test(newValue.phone) && newValue.code.length === 6) {
					this.loginMobile = true;
				} else {
					this.loginMobile = false;
				}
			},
			deep: true
		},
	},
	methods: {
		// 登录按钮点击执行
		fnLogin() {
			uni.showLoading({
				title: "登陆中...",
				mask: true
			})
			this.$http.postMobilelogin({
				mobile: this.mobile.phone,
				captcha: this.mobile.code
			}, res => {
				if (res.code == 1) {
					this.$db.set("userinfo", res.data.userinfo);
					uni.hideLoading()
					uni.showToast({
						icon: "success",
						title: "登录成功"
					})
					setTimeout(() => {
						uni.navigateTo({
							url: "/pages/home/home"
						})
					}, 500)
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		// 获取用户电话
		getPhoneNumber(e) {
			let {
				errMsg,
				iv,
				encryptedData
			} = e.detail;
			let confirm_unionid = uni.getStorageSync('confirm_unionid') || '';
			if (errMsg == "getPhoneNumber:ok") { // 同意授权
				uni.hideLoading()
				// 检查登录态是否过期。
				WxAuth.checkSession().then(code => {
					//code, iv, encryptedData 传给服务器解析，得到手机号信息
					const data = {
						code,
						iv,
						encryptedData,
						confirm_unionid
					};
					this.$http.getMobile(data, res => {
						if (res.code == 1) {
							uni.showToast({
								title: "授权成功！"
							})
							this.mobile.phone = res.data.mobile;
							uni.hideLoading();
						}
					}).catch(err => {
						console.log(err)
					})
				}).catch(console.log)
			} else {
				// 拒绝手机号授权
				uni.showToast({
					title: '手机号授权失败！',
					icon: 'none'
				});
			}
		},
		// 获取验证码
		fnGetPhoneCode() {
			if (this.phoneReg.test(this.mobile.phone)) {
				uni.showToast({
					title: "正在发送验证码",
					icon: "loading",
					success: () => {
						// 成功后显示倒计时60s后可在点击
						this.safety.state = true;
						// 倒计时
						this.safety.interval = setInterval(() => {
							if (this.safety.time-- <= 0) {
								this.safety.time = 60;
								this.safety.state = false;
								clearInterval(this.safety.interval);
							}
						}, 1000);
						// 发送验证码
						this.$http.postSendCode({
							mobile: this.mobile.phone
						}, res => {
							if (res.code == 1) {
								uni.showToast({
									title: "发送成功",
									icon: "success"
								})
							} else {
								this.$common.errorToShow(res.msg);
							}
						})
					}
				})
			} else {
				uni.showToast({
					title: "手机号不正确",
					icon: "none"
				})
			}
		},
	}
}
